import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ModalController } from 'ionic-angular';
import { BackProvider } from '../../providers/back/back';
import { Observable } from 'rxjs/Observable';

@IonicPage()
@Component({
  selector: 'page-teams',
  templateUrl: 'teams.html',
})
export class TeamsPage {

  public teams$: Observable<any[]> = this.back.getAllTeams()

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private back: BackProvider,
    private modal: ModalController
  ) {
  }

  formatMembers(members: any[]) { return members.map(u => u.name + ' ' + u.lastName).join(', ') }

  async deleteTeam(id: string, i: number, teams: any[]) {
    await this.back.deleteBeca(id).toPromise()
    teams.splice(i, 1)
  }

  openTeamDetail(id: string) {
    const m = this.modal.create('TeamDetailPage', {id})
    m.present()
  }

}



